<!-- eslint-disable vue/valid-v-slot -->
<template>
  <v-dialog v-model="showDialog" draggable content-class="import-dialog">
    <div class="d-flex flex-column" style="height: 100%">
      <v-btn text class="close-button pa-2" @click="close"><v-icon>mdi-close</v-icon></v-btn>
      <div class="page-title px-6 pt-5">Import Waypoints</div>
      <div v-if="initial" class="page-title--sub px-6">Please load a JSON file of waypoints to begin.</div>
      <div v-else class="page-title--sub px-6">Use the UI below to import and merge waypoints into your working set.</div>
      <v-divider color="primary-blue" class="mt-4" />

      <div class="d-flex align-center my-4 mx-6" style="width: fit-content">
        <v-file-input v-model="uploadedFile" outlined show-size hide-details dense accept=".json" class="file-input mr-3" placeholder="Select JSON File" />
        <v-btn dense class="select-path-button mr-3" outlined @click="onLoadData">Load Data</v-btn>
        <div class="import-info">{{ importInfoText }}</div>
      </div>

      <div class="flex-grow-1">
        <v-data-table
          v-model="loadedData"
          :items="loadedData"
          class="waypoint-list"
          :items-per-page="-1"
          fixed-header
          hide-default-footer
          disable-pagination
          :headers="tableHeaders"
        >
          <template v-slot:item.name="{ item }">
            <div class="d-flex align-center">
              <div class="image-wrapper" :style="{ 'background-color': item.color }">
                <img :src="ICON_MAP[item.icon].workingFilePath" contain width="32px" height="32px" style="filter: invert(1)" />
              </div>
              <span class="pl-3"> {{ item.name }}</span>
            </div>
          </template>
          <template v-slot:header.select>
            <div class="d-flex align-center"> <v-checkbox v-model="allChecked" @change="flipAllChecked" /> Select </div>
          </template>
          <template v-slot:item.select="{ item }">
            <div class="d-flex justify-center">
              <v-checkbox v-model="checkedWaypoints" multiple :value="item.id" />
            </div>
          </template>
          <template v-slot:item.position="{ item }">
            <div class="d-flex">
              {{ `[${item.position.x}, ${item.position.y}, ${item.position.z}]` }}
            </div>
          </template>
          <template v-slot:item.actions="{ item }">
            <div class="d-flex">
              <v-btn class="action-button view mr-2" dense outlined @click="onView(item)">View</v-btn>
              <v-btn class="action-button mr-2" :class="{ show: item.hide, hide: !item.hide }" dense outlined @click="onShowHide(item)">{{
                item.hide ? 'Show' : 'Hide'
              }}</v-btn>
              <v-btn class="action-button delete" dense outlined @click="onDelete(item)">Delete</v-btn>
            </div>
          </template>
          <template v-slot:no-data>
            <div class="d-flex flex-column">
              <div class="pt-2">Load a JSON file.</div>
              <div class="pt-2 pb-4">If you have already loaded one, the JSON file may be bad.</div>
            </div>
          </template>
          <template v-slot:group.header="{ group, toggle, isOpen }">
            <td colspan="3" class="group-header-row" @click="toggle">
              <div class="d-flex justify-center align-center group-header-text">
                <v-icon size="32" class="mr-3">{{ isOpen ? 'mdi-minus' : 'mdi-plus' }}</v-icon>
                <div class="group-header"> "{{ group }}" Waypoints </div>
                <v-icon size="32" class="ml-3">{{ isOpen ? 'mdi-minus' : 'mdi-plus' }}</v-icon>
              </div>
            </td>
          </template>
        </v-data-table>
      </div>

      <v-divider color="primary-blue" style="margin-top: auto" />
      <div class="d-flex my-4 mx-6 align-center">
        <div> When you import waypoints, they will be <strong>merged</strong> into your current set. Waypoints that have duplicate IDs will be skippped. </div>
        <v-btn dense class="select-path-button px-10 py-5" style="margin-right: 0; margin-left: auto" outlined @click="onImport">Import</v-btn>
      </div>
    </div>
  </v-dialog>
</template>

<script>
import { ref, watch, inject } from '@vue/composition-api';

import { useStorage } from '@/models/useStorage.js';
import { useMap } from '@/models/useMap.js';
import { useCoordinates } from '@/models/useCoordinates.js';

import { ICON_MAP } from '@/models/useIcons.js';

export default {
  name: 'ImportDialog',

  props: {
    initial: {
      type: Boolean,
      default: false,
    },
  },

  setup() {
    const masterMapData = inject('masterMapData');
    const masterPointsArray = inject('masterPointsArray');
    const showDialog = inject('showImportDialog');

    const uploadedFile = ref(null);
    const loadedData = ref([]);
    const allChecked = ref(true);

    const { readFromJSON } = useStorage();
    const { mergePoints } = useMap(masterMapData, masterPointsArray);

    const { scaleDownCoordinate } = useCoordinates();

    const tableHeaders = [
      {
        text: 'Select',
        align: 'start',
        sortable: false,
        value: 'select',
      },
      {
        text: 'Name',
        align: 'start',
        sortable: true,
        value: 'name',
      },
      {
        text: 'Coordinate',
        align: 'start',
        sortable: false,
        value: 'position',
      },
      {
        text: 'Group',
        align: 'start',
        sortable: true,
        value: 'group',
      },
    ];

    const checkedWaypoints = ref(null);

    return {
      showDialog,
      uploadedFile,
      loadedData,
      readFromJSON,
      tableHeaders,
      allChecked,
      ICON_MAP,
      checkedWaypoints,
      scaleDownCoordinate,
      mergePoints,
    };
  },

  computed: {
    importInfoText() {
      return `${this.loadedData ? this.loadedData.length : '0'} Loaded Waypoints`;
    },
  },

  watch: {
    checkedWaypoints() {
      this.allChecked = this.checkedWaypoints?.length === this.loadedData?.length;
    },
  },

  methods: {
    async onLoadData() {
      this.loadedData = await this.readFromJSON(this.uploadedFile);
      this.checkedWaypoints = this.loadedData.map((obj) => {
        return obj.id;
      });
    },

    onImport() {
      let selectedPoints = this.loadedData.filter((obj) => {
        return this.checkedWaypoints.includes(obj.id);
      });

      let scaledDownPoints = selectedPoints.map((obj) => {
        return this.scaleDownCoordinate(obj);
      });

      this.mergePoints(scaledDownPoints);
      this.close();
    },

    flipAllChecked() {
      if (this.allChecked) {
        this.checkedWaypoints = this.loadedData.map((obj) => {
          return obj.id;
        });
      } else {
        this.checkedWaypoints = [];
      }
    },

    open() {
      this.showDialog = true;
    },

    close() {
      this.showDialog = false;
    },
  },
};
</script>

<style lang="scss">
@use 'sass:color';

@import '@/design/variables/_colors';

.import-dialog {
  position: relative;
  max-width: 70vw;
  height: 90vh;
  overflow: hidden;
  background: #333;
}
</style>

<style lang="scss" scoped>
@use 'sass:color';

@import '@/design/variables/_colors';

.close-button {
  position: absolute;
  top: 16px;
  right: 16px;
  width: fit-content;
  color: white;
}

.page-title {
  font-size: 32px;
  color: white;
  letter-spacing: 0.04em;

  &--sub {
    font-size: 16px;
    color: white;
    letter-spacing: 0.02em;
  }
}

.select-path-button {
  height: 40px !important;
  color: $primary-blue !important;
  text-transform: none;
  letter-spacing: 0.01em;
}

.file-input::v-deep {
  color: $primary-blue !important;
  text-transform: none;
  letter-spacing: 0.01em;

  button {
    color: $primary-blue !important;
  }

  .v-file-input__text {
    color: $primary-blue !important;
  }

  fieldset {
    color: $primary-blue !important;
  }

  .v-input__slot {
    &:hover {
      cursor: pointer;
      background: color.change($primary-blue, $alpha: 0.2%, $lightness: 40%, $saturation: 30%);
    }
  }
}

.waypoint-list::v-deep {
  background-color: transparent !important;

  .v-data-table__wrapper {
    max-height: calc(90vh - 260px);
  }

  .image-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border-radius: 16px;
  }

  th {
    color: white !important;
    background: #222 !important;

    i {
      margin-left: 4px;
      color: white !important;
      opacity: 0.5;
    }
  }

  tr {
    background-color: transparent !important;

    &:hover {
      background: #444 !important;
    }
  }

  td {
    color: white;
    border-bottom: 1px solid #666 !important;
  }

  .v-row-group__header {
    background: color.change($primary-blue, $lightness: 60%, $saturation: 50%) !important;
  }

  .group-header-text {
    height: 24px;
    font-size: 18px;
    font-weight: 600;
    color: black;
    letter-spacing: 0.03em;
  }

  .group-header-row {
    height: 26px;
    padding: 0 !important;

    &:hover {
      cursor: pointer;
      background: color.change($primary-blue, $lightness: 40%, $saturation: 50%) !important;
    }
  }
}
</style>
